




























































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:'',
			name:'',
			list:[]
		}
	},
	onLoad() {
		this.static=ut.static;
	},
	methods: {
		req_goodslist(){
			ut.request({
				data: {
					name:this.name
				},
				url: "class/goods/list"
			}).then(data=>{
				this.list=data;
				if(!this.list.length){
					ut.totast('暂无相关商品')
				}
			})
		},
		go_build_mallinf(_id,name){
			wx.navigateTo({
				url: `../build/mallinf?_id=${_id}&&title=${name}`
			})
		}
	}
}
